import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Progress from "../components/Progress";

function ProgressPage({ setLoginStatus }) {
  const [infos, setInfos] = useState();

  const navigate = useNavigate();

  const onClickChangePage = () => {
    navigate(`/download`);
  };

  const onClickLoadInfos = () => {
    const body = {};
    fetch("http://localhost:4000/predictioninfo", {
      method: "post", // 통신방법
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify(body),
    })
      .then((response) => response.json())
      .then((res) => {
        let finalList = [];
        for (let i = 0; i < res.length; i++) {
          let curElement = res[i];
          let userId = curElement["user_id"];
          let groupId = curElement["group_id"];
          let progress = 0;
          for (const [key, value] of Object.entries(curElement)) {
            if (
              key.indexOf("init_price") !== -1 ||
              key.indexOf("whether_to_change") !== -1 ||
              key.indexOf("final_price") !== -1
            ) {
              if (value != null) {
                progress += 1;
              }
            }
          }
          let curRes = [userId, groupId, progress, 30];
          finalList.push(curRes);
        }

        if (finalList) {
          let realFinalResult = [];
          let tempResult = [];
          for (let i = 0; i < finalList.length; i++) {
            tempResult.push(finalList[i]);

            if (i % 4 === 3) {
              let res = (
                <Progress setLoginStatus={setLoginStatus} infos={tempResult} />
              );
              realFinalResult.push(res);
              tempResult = [];
            }
          }
          setInfos(realFinalResult);
        }

        // let realFinalResult = [];
        // let tempResult = [];
        // for (let i = 0; i < 4; i++) {
        //   tempResult.push([i + 1, 1, i + 5, 30]);

        //   if (i % 4 == 3) {
        //     let res = (
        //       <Progress setLoginStatus={setLoginStatus} infos={tempResult} />
        //     );
        //     realFinalResult.push(res);
        //     tempResult = [];
        //   }
      });
  };

  return (
    <div>
      <h1>Survey Progress Status</h1>
      <button type="button" onClick={onClickLoadInfos}>
        Loading informations
      </button>
      <button type="button" onClick={onClickChangePage}>
        Go to Download page
      </button>
      {infos}
    </div>
  );
}

export default ProgressPage;
